					content: '曾经意外 他和她相爱 在不会犹豫的时代 以为明白 所以爱得痛快 一双手紧紧放不开 心中的执着与未来 忘不了你的爱 但结局难更改 我没能把你留下来 更不像他能给你一个期待的未来 幼稚的男孩 你的关怀一直随身携带 无人的地方再打开 想问你现在是否忧伤不再 像躺在阳光下的海 像用心涂抹的色彩 让你微笑起来 勇敢起来 忘不了你的爱 但结局难更改 我没能把你留下来 更不像他能给你一个期待的未来 幼稚的男孩',
					choices: [
						{
							Eindex: 'A',
							content: '出现又离开'
						},
						{
							Eindex: 'B',
							content: '男孩'
						},
						{
							Eindex: 'C',
