	methods: {
		activeChoice (e){
			var arryindex = this.isActive.indexOf(e);
			if(arryindex == -1){
				this.isActive.push(e)
			}else{
				this.isActive.splice(arryindex,1)
			}
		},
		preQuestion (){
			if(this.number == 1){
				return
			}else{
				this.content = this.questionlist[this.number-2].content;
				this.choices = this.questionlist[this.number-2].choices;
				this.number--;
				this.isActive = 99;
			}
		},
		nextQuestion (){
			this.content = this.questionlist[this.number].content;
			this.choices = this.questionlist[this.number].choices;
			this.number++;
			this.isActive = 99;
		}
